/* src/components/home.css */

.nav-bar {
    background-color: #f8f9fa; /* Example background color */
    padding: 1rem;
    border-bottom: 1px solid #ddd;
  }
  
  .nav-bar ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
    gap: 1rem;
  }
  
  .nav-bar li {
    display: inline;
  }
  
  .nav-bar a {
    text-decoration: none;
    color: #007bff;
    font-weight: bold;
  }
  
  .nav-bar a:hover {
    text-decoration: underline;
  }
  